.app_wraper{
  width: 1200px;
  margin: auto;
  padding: 0;
  display: grid;
  grid-template-areas: 
  "h h"
  "n c" ; 
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  background-color: rgb(226, 226, 226);
} 
.app_wraper_content{
    grid-area: c;
    background-color: rgb(235, 233, 233);
    margin: 1% 1%;
}
